
.remoteBlock {
    background-color: #343a40;
    position: relative;
}

.remoteContainer {
    justify-content: flex-start;
    flex-direction: column;
    padding-bottom: 30px;
}

.remoBtn {
    width: 40px;
    cursor: pointer;
    margin-top: 30px;
    position: relative;
    border-radius: 50%;
    background: #3350c2;
}

.remoBtn::before {
    content: '';
    position: absolute;
    border: 2px solid #3D64FF;
    left: -20px;
    opacity: 0;
    right: -20px;
    top: -20px;
    bottom: -20px;
    border-radius: 50%;
    animation: remoBtn 2.5s linear infinite;
}

@keyframes remoBtn {
    0% {
        transform: scale(0.5);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: scale(1.2);
        opacity: 0;
    }
}

.remoBtn::after,
.remoBtn::before {
    content: '';
    position: absolute;
    border: 2px solid #3D64FF;
    left: -20px;
    opacity: 0;
    right: -20px;
    top: -20px;
    bottom: -20px;
    border-radius: 50%;
    animation: remoBtn 2.5s linear infinite;
}

.remoBtn::after {
    animation-delay: 1.25s;
}

@keyframes remoBtn {
    0% {
        transform: scale(0.5);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: scale(1.2);
        opacity: 0;
    }
}

.remoBtn a {
    position: relative;
    z-index: 10;
}

